<template>
  <div class="occupy-wrapper">
    <p class="title">请选择被保险人<span>{{this.$route.query.name}}</span>的职业</p>
    <ul class="select-box">
      <li class="select-item" :class="select_text1?'active':''" @click="getSelect1()">
        {{select_text1||'行业'}}
      </li>
      <li class="select-item" v-show="select_text1" :class="select_text2?'active':''"
          @click="getSelect2()">{{select_text2||'细分行业'}}
      </li>
      <li class="select-item" v-show="select_text2" :class="select_text3?'active':''">{{select_text3||'职业'}}</li>
    </ul>
    <div class="occupy-con">
      <ul class="occupations" v-show="occupations1 && !occupations2.length && !occupations3.length">
        <li class="text" v-for="(occupation,index) in occupations1" :key="occupation.id"
            @click="selectOccupy1(occupation,index)"
        >
          {{occupation.first_name}}
        </li>
      </ul>
      <ul class="occupations" v-show="occupations2 && occupations2.length &&!occupations3.length">
        <li class="text" v-for="(occupation,index) in occupations2" :key="occupation.id"
            @click="selectOccupy2(occupation,index)"
        >
          {{occupation.second_name}}
        </li>
      </ul>
      <ul class="occupations" v-show="occupations3">
        <li class="text" v-for="(occupation,index) in occupations3" :key="occupation.id"
            @click="selectOccupy3(occupation,index)"  :class="currentCategoryIndex==index? 'active':''"
        >
          {{occupation.name}}
					<img src="@/assets/images/selected.png" class="selected-img">
        </li>
      </ul>
    </div>
    <div @click="goNext()" class="nextstep" :class="selectCode?'active':''">提交</div>
    <occupy-alert :title="alertTitle" v-if="occupyAlertShow" @handleBtnRightEvent="occupyAlertShow=false"></occupy-alert>
  </div>

</template>

<script>
import occupyAlert from '@/components/product/TheOccupyAlert'
import { setLocalStorage, removeLocalStorage, getLocalStorage } from '@/assets/js/localStorage'
export default {
  name: 'occupy',
  data () {
    return {
      occupations1: [],
      occupations2: [],
      occupations3: [],
      select_text1: '',
      select_text2: '',
      select_text3: '',
      dataSuccess: false,
      currentCategoryIndex: undefined,
      selectCode: '',
      selectType: '',
      selectId: '',
      alertTitle: '',
      occupyAlertShow: false,
      postInsured: {}
    }
  },
  components: {
    occupyAlert
  },
  mounted () {
    this.getOccupation () 
    this.postInsured = getLocalStorage('postInsured')
  },
  methods: {
    getSelect1 () {
      this.getOccupation().then((res) => {
        this.occupations1 = res.data
        this.currentCategoryIndex = undefined
        this.occupations2 = ''
        this.occupations3 = ''
        this.select_text1 = ''
        this.select_text2 = ''
        this.select_text3 = ''
        this.selectCode = ''
      })
    },
    getSelect2 () {
      this.getOccupation(this.selectId).then((res) => {
        this.currentCategoryIndex = undefined
        this.occupations1 = ''
        this.occupations2 = res.data
        this.occupations3 = ''
        this.select_text2 = ''
        this.select_text3 = ''
        this.selectCode = ''
      })
    },
    getOccupation (id) {
      return new Promise((resolve, reject) => {
        let postData = {
          id: id
        }
        !id ?
				this.$api.product.occupySelect().then(res => {
				  this.occupations1 = res.data
				  this.$nextTick(() => {
				    this.dataSuccess = true
						resolve(res)
				  })
				}) : 
				this.$api.product.occupySelect(postData).then(res => {
          this.occupations1 = res.data
          this.$nextTick(() => {
            this.dataSuccess = true
            resolve(res)
          })
        })
      })
    },
    selectOccupy1 (occupation, index) {
      this.select_text1 = occupation.first_name
      this.selectId = occupation.id
      if (occupation.id) {
        this.getOccupation(occupation.id).then((res) => {
          this.occupations2 = res.data
        })
      }
    },
    selectOccupy2 (occupation, index) {
      this.select_text2 = occupation.second_name
      this.occupations3 = occupation.detail
    },
    selectOccupy3 (occupation, index) {
      this.currentCategoryIndex = index
      this.select_text3 = occupation.name
      this.selectCode = occupation.value
      this.selectType = occupation.type
      this.$emit('occupySelect', this.select_text3, this.selectCode, this.selectType)
    },
    goNext () {
      if (this.selectCode == '') {
        return
      }
      if (this.selectType > 4) {
        this.occupyAlertShow = true
        this.alertTitle = `非常抱歉！<br/>
        被保险人的职业不符合要求的1-4类职业`
      } else {
        this.postInsured.occupationCode = this.selectCode
        this.$router.push({
          path: '/product/healthInfo',
          query: {
            productId: this.$route.query.productId,
            name: this.$route.query.name
          }
        })
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (to.name == 'HealthInfo') {
      setLocalStorage('postInsured', this.postInsured)
    } else {
      removeLocalStorage('insured')
    }
    next()
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .occupy-wrapper {
    background: #fff;
    height: 100%;
    .title {
      height: pxToRem(68px);
      line-height: pxToRem(68px);
      font-size: pxToRem(28px);
      color: #B6BBBB;
      padding-left: pxToRem(30px);
      background: #F5F7F7;
      span {
        color: #000000;
      }
    }
    .select-box {
      width: 100%;
      height: pxToRem(80px);
      line-height: pxToRem(80px);
      border-bottom: $border-bottom-light-color;
      box-sizing: border-box;
      .select-item {
        float: left;
        width: 33.33%;
        text-align: center;
        font-size: 14px;
        color: $color-active-text;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        border-left:1px solid  #EDEDED;;
        box-sizing: border-box;
        &:first-child {
          border-left: none;
        }
        &.active {
          color: $color-text;
        }
      }
    }
    .occupy-con {
      position: absolute;
      box-shadow: 0 0 4px 0 rgba(219, 219, 219, 0.35);
      z-index: 20;
      top: pxToRem(148px);
      right: 0;
      left: 0;
      bottom:pxToRem(98px);
      overflow: scroll;
      background: #fff;
      /*height: calc(100% - 40px);*/
      .occupations {
        .text {
          font-size: pxToRem(28px);
          color: $color-text;
          margin-left:pxToRem(30px);
          line-height: pxToRem(40px);
          padding:pxToRem(24px) pxToRem(30px) pxToRem(24px);
          border-bottom: $border-bottom-light-color;
					position: relative;
          &.active {
            .selected-img{
							display: block;
						}
          }
					.selected-img{
						display: none;
						position: absolute;
						bottom: pxToRem(34px);
						right: pxToRem(34px);
						width: pxToRem(30px);
						height: pxToRem(21px);
					}
        }
      }
    }
      .nextstep {
        height:pxToRem(98px);
        line-height: pxToRem(98px);
        text-align: center;
        font-size: pxToRem(34px);
        color: #fff;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        max-width: 768px;
        margin: 0 auto;
        right: 0;
        z-index: 102;
        background: $color-active-text;
        opacity: 0.45;
        &.active {
          opacity: 1;
        }
      }
  }

</style>
