<template>
  <div class="alert-occupy-box"  >
    <div class="occupy-wrapper" :style="popStyle">
      <p class="title" v-html="title"></p>
      <div class="alert-btn-wrapper clearfix">
        <p class="alert-btn alert-btn-left" @click="handleBtnEventLeft">选择其他保险</p>
        <p class="alert-btn alert-btn-right" @click="handleBtnEventRight">确定</p>
      </div>
    </div>
    <div class="cover"></div>
  </div>
</template>

<script>
export default {
  name: 'BaseAlertBox',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  methods: {
    handleBtnEventLeft () {
      this.$router.push({
        path: '/product/list'
      })
    },
    handleBtnEventRight () {
      this.$emit('handleBtnRightEvent')
    }
  },
  computed: {
    popStyle () {
      let wHeight = document.documentElement.clientHeight || document.body.clientHeight
      return {
        top: (wHeight - 144-86) / 2 + 'px'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  .occupy-wrapper {
    position: absolute;
    z-index: 104;
    left: pxToRem(90px);
    background: #fff;
    width: pxToRem(570px);
    height: pxToRem(288px);
    border-radius: pxToRem(12px);
    .title {
      font-size: pxToRem(30px);
      color: $color-text;
      margin-top:pxToRem(50px);
      text-align: center;
      line-height: pxToRem(48px);
    }
    .alert-btn-wrapper {
      text-align: right;
      border-top: 1px solid #EDEDED;
      margin-top:pxToRem(50px);
      display: flex;
      justify-content: space-between;
      .alert-btn {
        width: 50%;
        cursor: pointer;
        font-size:pxToRem(30px);
        color: #A5A8A8;
        height: pxToRem(91px);
        line-height:  pxToRem(91px);
        text-align: center;
        background: #FFFFFF;
        border-right: 1px solid  #EDEDED;
        box-sizing: border-box;
      }
      .alert-btn-left {

        color: #B6BBBB;
      }
      .alert-btn-right {
        color: $color-active-text;
      }
    }

  }
  .cover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 103;
    background: rgba(28, 29, 29, 0.7);
  }
</style>
